import React from 'react';
import { Formik, Form, Field,ErrorMessage } from 'formik';
import './Contact.css';
import { TextField, Radio, FormControlLabel, FormLabel, FormControl, RadioGroup } from "@material-ui/core"
import * as yup from 'yup';
import { FormHelperText } from '@material-ui/core';
import database from '../firebase';

function Contact() {
    const initialValues = {
        firstname: "",
        lastname: "",
        email: "",
        phone: "",
        website: "",
        message: ""
    }
    const onsubmit = (values, props) => {
        // console.log(values);
        // console.log(props);
        database.ref("user").set({
           values:values
        }).catch(alert);
        setTimeout(() => {

            props.resetForm()
            props.setSubmitting(false)
        }, 1000)
    }
    const validationSchema = yup.object().shape({
        firstname: yup.string().min(3,"Its too short").required("Required"),
        lastname: yup.string().min(3,"Its too short").required("Required"),
        email: yup.string().email("Enter valid Email").required("Required"),
        phone: yup.number().typeError("Enter a valid Phone Number").required("Required"),
        website: yup.string().oneOf(["webdesign","webdevelopment","logodesign","other"],"Required").required("Required"),
        message:  yup.string().min(3,"Its too short").required("Required")
    })
    return (
        <div className="container m-auto bg-light bg-gradient mt-5">
            <div className="text-center p-5">
                <h1 className="hcontact fw-bold">Contact Us</h1>
                <h5 className="  text-muted"> Any questions or remarks? just write us a message! </h5>
            </div>
            <div className="row gx-0 m-auto p-3 main-contact rounded justify-content-center ">
                <div className="col-md-5 left-info">
                    <div className="p-5">
                        <h4 className="">Contact Informtion</h4>
                        <p className="fillup fw-light">Fill up the form our team will get back to you within 24 hours.</p>

                        <div className="mt-5 icondiv"> <i className="fas fa-phone-alt"></i> <span className="icons">+0984-567-8955</span></div>
                        <div className="mt-5 icondiv" ><i className="fas fa-envelope"></i><span className="icons">1hlo@flowbse.com</span></div>
                        <div className="mt-5 icondiv mb-0"><i className="fas fa-map-marker-alt"></i><span className="icons">102 Street 0452 Don</span></div>
                    </div><div><div className="circle"></div> <div className="quarter-circle" ></div> </div>
                    <div className="mt-5 text-center"><span className="footer-icons" ><i className="fab fa-facebook-f footer"></i></span>
                        <span className="footer-icons"><i className="fab fa-twitter footer"></i>
                        </span> <span className="footer-icons"><i className="fab fa-instagram footer"></i></span>
                        <span className="footer-icons"><i className="fab fa-linkedin-in footer"></i></span></div>
                </div>



                <div className="col-md-7 ">
                    <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onsubmit} >
                        {
                            (props) => (<Form>
                                <div className="mb-4">
                                    <Field as={TextField} label='First Name' placeholder='' className="textfield" 
                                    name="firstname" helperText={<ErrorMessage name="firstname"/>} />

                                    <Field as={TextField} label='Last Name' className="textfield" 
                                    placeholder='john doe' name="lastname" helperText={<ErrorMessage name="lastname"/>}/>

                                    <Field as={TextField} label='Mail' placeholder='john@' type="email" 

                                    className="textfield" name="email"  helperText={<ErrorMessage name="email"/>}/>

                                    <Field as={TextField} label='Phone' className="textfield" type="number" placeholder='99' name="phone" 
                                        helperText={<ErrorMessage name="phone"/>}
                                    />

                                </div>
                                <div className="p-4">
                                    <FormControl component="fieldset">

                                        <FormLabel component="legend">What the of Website do you need? </FormLabel>
                                        <Field as={RadioGroup} row aria-label="gender" name="website" >
                                            <FormControlLabel value="webdesign" control={<Radio />} className="r-color" label="Web Design " />
                                            <FormControlLabel value="webdevelopment" control={<Radio />} label="Web Development" className="r-color" />
                                            <FormControlLabel value="logodesign" control={<Radio />} label="Logo Design" className="r-color" />
                                            <FormControlLabel value="other" control={<Radio />} label="Other" className="r-color" />

                                        </Field>
                                    </FormControl>
                                    <FormHelperText> {<ErrorMessage name="website"/>}</FormHelperText>
                                </div>
                                <div className="p-3"> <Field as={TextField} label='Message' placeholder='Write Your Message' name="message"
                                 fullWidth type="textarea" helperText={<ErrorMessage name="message"/>} />
                                </div>
                                <div className=" text-end mt-5">
                                    <button type="submit" className="btn px-auto py-auto btn-lg me-md-2 send-m"
                                    disabled={props.isSubmitting}> {props.isSubmitting ? "Loading" : "Send Messaage"}</button></div>
                            </Form>)
                        }
                    </Formik>
                </div>

            </div>
        </div>
    )
}

export default Contact;
